    text-rendering: optimizeLegibility;
  }
  body {
    min-height: 100vh;
    position: relative;
    isolation: isolate;
    background-color: hsl(var(--background) / 0.9);
    background-image: var(--app-gradient);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    color: hsl(var(--foreground));
  }
  h1,
  h2,
  h3,
  h4,
  h5 {
    font-family: "Space Grotesk", system-ui, sans-serif;
  }
  @keyframes float {
    0%,
    100% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-10px);
    }
  }
  .animate-float {
    animation: float 6s ease-in-out infinite;
  }
  .animate-float-slow {
    animation: float 9s ease-in-out infinite;
  }
  body::before {
    content: "";
    position: fixed;
    inset: 0;
    pointer-events: none;
    z-index: -2;
    background-image: var(--app-gradient);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }
  .dark body::after {
    content: "";
    position: fixed;
    inset: -18%;
    pointer-events: none;
    z-index: -1;
    background-image: radial-gradient(48% 40% at 8% 8%, hsl(var(--primary) / 0.5), transparent 65%),
      radial-gradient(45% 45% at 92% 12%, hsl(var(--accent) / 0.4), transparent 64%),
      radial-gradient(55% 50% at 50% 110%, hsl(251 92% 62% / 0.28), transparent 68%);
    filter: blur(14px) saturate(1.18);
  }
  .light body::after {
    content: "";
    position: fixed;
    inset: -20%;
    pointer-events: none;
    z-index: -1;
    background-image: radial-gradient(48% 40% at 8% 8%, hsl(var(--primary) / 0.25), transparent 65%),
      radial-gradient(45% 45% at 92% 12%, hsl(var(--accent) / 0.2), transparent 64%);
    filter: blur(18px) saturate(1.05);
  }
  .bg-background,
  .bg-card,
  .bg-popover,
  .bg-sidebar {
    background-color: hsl(var(--background) / 0.34);
    background-image: linear-gradient(var(--app-surface-tint), var(--app-surface-tint));
    backdrop-filter: blur(16px);
  }
}
